<template>
  <div class="tap-control">
    <div class="tap-contriol-item"
         v-for="(item,index) in hometabs"
         :class="{active: index == $store.state.activeindex }"
         @click="activeChange(index)">
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "tapControl",
    props:{
      hometabs:{
        type:Array,
        default(){
          return []
        }
      }
    },
    data(){
      return{

      }
    },
    methods:{
      activeChange(index){
        this.$store.commit('changeActiveItem',index)
        //向父组件发射一个事件
        this.$emit('itemclick')
      }
    }
  }
</script>

<style scoped>
  .tap-control{
    width: 100%;
    display: flex;
    text-align: center;
    flex: 15px;
    background-color: white;
    height: 40px;
  }
  .tap-contriol-item{
    flex: 1;
    height: 40px;
    line-height: 40px;
    transition: 0.5s;
  }
  .active{
    color: var(--color-tint);
  }
</style>
