<template>
  <swiper>
    <swiper-item v-for="item in banners">
      <a :href="item.link">
        <img :src="item.image" alt="" @load="SwiperImgLoaded">
      </a>
    </swiper-item>
  </swiper>
</template>

<script>
  import {Swiper, SwiperItem} from 'components/common/swiper/index'

  export default {
    name: "mainSwiper",
    components: {
      Swiper,
      SwiperItem
    },
    props: {
      banners: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data(){
      return{
        needEmit:true
      }
    },
    methods:{
      SwiperImgLoaded(){
        if (this.needEmit){
          this.$emit("swiperimgloaded")
          this.needEmit = false
        }
      }
    }

  }
</script>

