<template>
  <div id="home">
    <top-nav class="home-topbar">
      <div slot="center">购物街</div>
    </top-nav>
    <tap-control :hometabs="['流行','新款','精选']"
                 class="tapcontrolhidden"
                 @itemclick="changeItem"
                 :class="{tapcontrolfixed:needtapfixed}"></tap-control>
    <Scroll ref="scroller"
            :probe-type="3"
            @bescroll="Bescroll"
            :pull-up-load="true"
            @pullingUp="upDateGoods">
      <main-swiper :banners="banners" @swiperimgloaded="showTpclTop"></main-swiper>
      <home-recommend :recommends="recommends"></home-recommend>
      <img-recommend></img-recommend>
      <tap-control :hometabs="['流行','新款','精选']"
                   class="tapcontrol"
                   @itemclick="changeItem"
                   ref="tapcontrol01"></tap-control>
      <goods-list :goods="ShowingGoods"></goods-list>
    </Scroll>
    <ScrollBack @click.native="backToTop" v-show="isbackshow"></ScrollBack>
  </div>
</template>

<script>
  //导入获取后端数据的方法
  import {getHomeMultidata, getHomeGoods} from "@/network/home";
  //导入工具
  import {debounce} from "@/common/utils";
  //导入组件
  import topNav from "@/components/common/topnav/topNav";
  import Scroll from "@/components/common/scroll/Scroll";
  import mainSwiper from "./childcop/mainSwiper";
  import homeRecommend from "./childcop/homeRecommend";
  import imgRecommend from "./childcop/imgRecommend";
  import tapControl from "@/components/content/tapcontrol/tapControl";
  import goodsList from "@/components/content/goodlist/goodsList";
  //导入混入
  import {BackTopMixin} from "@/common/mixin"

  export default {
    name: "Home",
    components: {
      topNav,
      mainSwiper,
      homeRecommend,
      imgRecommend,
      tapControl,
      goodsList,
      Scroll,
    },
    // mixins:[imgLoadListerMixinByActive],弃用原因：卡顿
    mixins:[BackTopMixin],
    data() {
      return {
        banners: [],
        recommends: [],
        goods: {
          'pop': {page: 0, list: []},
          'new': {page: 0, list: []},
          'sell': {page: 0, list: []}
        },
        adciveitem: 'pop',
        tapcontroltop: 0,
        needtapfixed: false,
        refresh: null
      }
    },
    activated() {
      // activated时,dom还未加载完成
      if (this.$refs.scroller.bscroll != null) {
        //通过ref获取到滑动组件内部的属性和方法
        // console.log(this.$store.state.savehomeY);
        this.$refs.scroller.bscroll.scrollTo(0, this.$store.state.savehomeY, 0);
        this.$refs.scroller.bscroll.refresh();

        //开启home组件监听imghasloaded事件
        //防抖,减少refresh函数调用的次数
        this.refresh = debounce(this.$refs.scroller && this.$refs.scroller.refresh, 50)
        //每加载完成一张图片就调用一次this.$refs.scroller.bscroll.refresh()以确保高度更新
        this.$bus.$on('imghasloaded', this.refresh)
      }
    },
    deactivated() {
      if (this.$refs.scroller.bscroll != null) {
        this.$store.commit('changeHomeY', this.$refs.scroller.bscroll.y)
        // console.log(this.$store.state.savehomeY);
      }
      //关闭home组件对imghasloaded事件的监听
      this.$bus.$off('imghasloaded')
    },
    created() {
      this.getHomeMultidata()
      this.getHomeGoods('pop')
      this.getHomeGoods('new')
      this.getHomeGoods('sell')
    },
    computed: {
      ShowingGoods() {
        return this.goods[this.adciveitem].list
      }
    },
    methods: {
      //获取轮播图和展示数据
      getHomeMultidata() {
        getHomeMultidata().then(res => {
          this.banners = res.data.banner.list
          this.recommends = res.data.recommend.list
        }).catch(err => {
        })
      },
      //获取商品数据
      getHomeGoods(type) {
        const page = this.goods[type].page + 1
        getHomeGoods(type, page).then(res => {
          this.goods[type].list.push(...res.data.list)
          this.goods[type].page += 1
        }).catch(err => {
          console.log(err)
        })
      },
      changeItem() {
        console.log(this.$store.state.activeindex);
        //切换商品种类
        switch (this.$store.state.activeindex) {
          case 0:
            this.adciveitem = 'pop'
            break
          case 1:
            this.adciveitem = 'new'
            break
          case 2:
            this.adciveitem = 'sell'
            break
        }
        console.log(this.adciveitem);
      },
      //滚动监听
      Bescroll(position) {
        //监听回到顶部按钮是否显示
        this.IsBackTopshow(position)
        //监听物品分类是否固定
        if (-position.y >= (this.tapcontroltop - 44)) {
          this.needtapfixed = true
        } else {
          this.needtapfixed = false
        }
      },
      //下拉加载更多
      upDateGoods() {
        //收到子组件下拉到底部事件，添加新数据到数组
        this.getHomeGoods(this.adciveitem)
        // //重新计算可滚动高度
        // this.$refs.scroller.bscroll.refresh()
        //本次滚动完成，开启下一次下拉到底操作
        this.$refs.scroller.finishPullUp()
      },
      //监听幻灯片是否加载完毕，计算完毕后获取tapControl高度
      showTpclTop() {
        //$el可以获取到组件内部的元素
        this.tapcontroltop = this.$refs.tapcontrol01.$el.offsetTop
        // console.log(this.tapcontroltop)
      }
    },
  }
</script>

<style scoped>
  #home {
    padding-top: 44px;
    /*vh:viewport height 设置视口高度*/
    height: 100vh;
    position: relative;
  }

  .home-topbar {
    background-color: var(--color-tint);
    color: white;
    position: fixed;
    top: 0;
    z-index: 9;
  }

  .tapcontrol {
    /*position: sticky;*/
    top: 43px;
    /*position: fixed;*/
  }

  .tapcontrolhidden {
    position: fixed;
    opacity: 0;
  }

  .wrapper {
    height: calc(100% - 49px);
  }

  .tapcontrolfixed {
    z-index: 9;
    opacity: 1;
  }

</style>
